export default {
  name: 'comHeader',
  props: {
    title: String
  },
  data () {
    return {
      isBack: true
    }
  },
  mounted () {
    console.log(window.history.length)
    if (Number(window.history.length) <= 0) {
      this.isBack = false
    }
  },
  methods: {
    onBack () {
      this.$router.back(-1)
    }
  }
}



